import { Injectable } from '@angular/core';

import { ValidatorFn, AbstractControl } from '@angular/forms';


@Injectable()
export class ValidManagerProvider {

  constructor() {
    console.log('Hello ValidManagerProvider Provider');
  }


  checkEmail(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      var re = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
      let input = control.value;
      let isValid = re.test(input);
      console.log('va: ', isValid)
      if (!isValid)
        return { 'email_check': { isValid } }
      else
        return null;
    };
  }

  checkPassword(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      var re = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[$@$!%*?&])[A-Za-z\d$@$!%*?&]{8,15}/;
      let input = control.value;
      let isValid = re.test(input);
      console.log('va: ', isValid)
      if (!isValid)
        return { 'newpassword_check': { isValid } }
      else
        return null;
    };
  }


}



